//
// Link buttons
//

// Make a button look and behave like a link
.vf-btn--link {
  position: relative;
  font-weight: 500;
  color: #222222;
  text-decoration: none;
  background-color: transparent;

  // For the arrow
  transform: translateZ(1);
  padding-left: 25px;

  &:before {
    top: calc(50% + 1px);
    transform: translate(-200%, -50%);
    font-weight: 400;
    font-size: 20px;
    color: $vattenfall-mid-blue;
  }

  &:focus:before, &:active:before, &:hover:before {
    transform: translate(-150%, -50%);
  }


  @include hover {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
    background-color: transparent;
    border-color: transparent;
  }

  &:focus,
  &.focus {
    text-decoration: $link-hover-decoration;
    border-color: transparent;
    box-shadow: none;
  }

  &:disabled,
  &.disabled {
    color: $btn-link-disabled-color;
    pointer-events: none;
  }

  // No need for an active state here
}

.vf-btn--link-white {
  color: #ffffff;

  &:before {
    color: inherit;
  }
  @include hover {
    color: $vattenfall-mid-blue;
  }
}

.vf-btn--link-with-arrow {
  padding-left: 30px;

  &:before {
    font-size: 28px;
    opacity: 1;
  }
}

//
// Button Sizes
//

.vf-btn--lg {
  @include button-size($btn-padding-y-lg, $btn-padding-x-lg, ($font-size-base * 1.125), $btn-line-height-lg, $btn-border-radius-lg);
  min-width: 240px;
}

.vf-btn--sm {
  @include button-size($btn-padding-y-sm, $btn-padding-x-sm, $font-size-sm, $btn-line-height-sm, $btn-border-radius-sm);
}


//
// Block button
//

.vf-btn--block {
  display: block;
  width: 100%;

  // Vertically space out multiple block buttons
  + .vf-btn--block {
    margin-top: $btn-block-spacing-y;
  }
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.vf-btn--block {
    width: 100%;
  }
}